@keyframes rotate360deg{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}button{padding:0;margin:0;font-size:1.4rem;flex-shrink:0;flex-grow:0;flex-basis:auto;text-align:center;background:none;border:0;transition:all .3s;cursor:pointer}button.fill,button.outline{display:inline-flex;justify-content:center;align-items:center;border-width:.1rem;border-style:solid;white-space:nowrap;border-radius:.4rem}button.text{text-decoration:underline}button.primary.fill{color:#fff;background-color:#ff4785;border-color:#ff4785}button.primary.fill:hover:not(:disabled):not(.isLoading){background-color:#e64078;border-color:#e64078}button.primary.outline{color:#ff4785;border-color:currentColor}button.primary.outline:hover:not(:disabled):not(.isLoading){color:#fff;background-color:#ff4785;border-color:#ff4785}button.primary.text{color:#ff4785}button.primary.text:hover:not(:disabled):not(.isLoading){color:#e64078}button.secondary.fill{color:#fff;background-color:#1ea7fd;border-color:#1ea7fd}button.secondary.fill:hover:not(:disabled):not(.isLoading){background-color:#1b96e4;border-color:#1b96e4}button.secondary.outline{color:#1ea7fd;border-color:currentColor}button.secondary.outline:hover:not(:disabled):not(.isLoading){color:#fff;background-color:#1ea7fd;border-color:#1ea7fd}button.secondary.text{color:#1ea7fd}button.secondary.text:hover:not(:disabled):not(.isLoading){color:#1b96e4}button.tertiary.fill{color:#fff;background-color:#45c98b;border-color:#45c98b}button.tertiary.fill:hover:not(:disabled):not(.isLoading){background-color:#3eb57d;border-color:#3eb57d}button.tertiary.outline{color:#45c98b;border-color:currentColor}button.tertiary.outline:hover:not(:disabled):not(.isLoading){color:#fff;background-color:#45c98b;border-color:#45c98b}button.tertiary.text{color:#45c98b}button.tertiary.text:hover:not(:disabled):not(.isLoading){color:#3eb57d}button.large:not(.text):not(.isOnlyIcon){height:50px;padding:0 24px;min-width:140px}button.large:not(.text):not(.isOnlyIcon):not(.isLoading){font-size:16px}button.large.isOnlyIcon{font-size:0;width:50px;height:50px;border-radius:100%}button.large.isOnlyIcon:empty:before{display:block;font-size:1.1rem;content:"Icon"}button.medium:not(.text):not(.isOnlyIcon){height:40px;padding:0 20px;min-width:90px}button.medium:not(.text):not(.isOnlyIcon):not(.isLoading){font-size:14px}button.medium.isOnlyIcon{font-size:0;width:40px;height:40px;border-radius:100%}button.medium.isOnlyIcon:empty:before{display:block;font-size:1.1rem;content:"Icon"}button.small:not(.text):not(.isOnlyIcon){height:32px;padding:0 8px;min-width:50px}button.small:not(.text):not(.isOnlyIcon):not(.isLoading){font-size:12px}button.small.isOnlyIcon{font-size:0;width:32px;height:32px;border-radius:100%}button.small.isOnlyIcon:empty:before{display:block;font-size:1.1rem;content:"Icon"}button.isFullWidth:not(.text):not(.isOnlyIcon){width:100%}button.isDisabled{opacity:.6;cursor:not-allowed}button.isLoading:not(.isOnlyIcon){font-size:0;user-select:none;cursor:progress}button.isLoading:not(.isOnlyIcon):before{display:block;content:"";flex-shrink:0;width:1.2rem;height:1.2rem;border:.2rem solid currentColor;border-top-color:transparent;border-radius:100%;animation:rotate360deg infinite 1s linear}button.isLoading:not(.isOnlyIcon) .loading{position:absolute;width:1px;height:1px;margin:-1px;padding:0;overflow:hidden;border:0;white-space:nowrap;clip:rect(0, 0, 0, 0)}