.myCheckbox{font-size:0}.myCheckbox input{position:absolute;width:1px;height:1px;margin:-1px;padding:0;overflow:hidden;border:0;white-space:nowrap;clip:rect(0, 0, 0, 0);cursor:pointer}.myCheckbox input+label{display:inline-flex;align-items:center;font-size:1.4rem;letter-spacing:-0.03rem;color:#222}.myCheckbox input+label:before{position:relative;display:inline-block;flex-shrink:0;width:1.8rem;height:1.8rem;margin-right:1rem;border-radius:.2rem;vertical-align:middle;background-color:#fff;background-size:2.4rem;background-repeat:no-repeat;background-position:50% 50%;border:1px solid #ccc;transition:background-color .25s,border-color .25s;content:""}.myCheckbox input:checked+label:before{background-image:url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' width='32' height='32'%3E%3Cpath d='M10.5 16.691 14.04 20l7.46-7' fill-rule='nonzero' stroke='%23ffffff' stroke-width='2' fill='none' stroke-linecap='square'/%3E%3C/svg%3E");background-color:#1ea7fd;border-color:#1ea7fd}.myCheckbox input:disabled+label{opacity:.6;cursor:not-allowed}.myCheckbox input:disabled:not(:checked)+label:before{border-color:#ccc;background-color:#e8e8e8}.myCheckbox input:hover:not(:checked):not(:disabled)+label:before{border-color:#444}